import { CanActivate, Router} from '@angular/router';
import { isLoggedin } from './is-loggedin';
import {FeatureAuthService} from './../services/feature-access.service';
@Injectable()
export class AnalyticsGuard implements CanActivate {

	constructor(private router: Router, private featureAuth : FeatureAuthService) {
		//this.getAccess();
	}

  	canActivate() {
   		return <boolean>this.featureAuth.getfeatureAccess('analytics');
  	}
	 /* canActivate() {
  return true;//this.featureAuth.getfeatureAccess("analytics");
 	}*/


}



