import { Injectable } from '@angular/core';
import { Http  } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import {  FeatureAccess } from '../interfaces/features.interface';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/of';
import 'rxjs/add/operator/catch';
import { SubDomainService } from './subdomain.service';
import { BaseService } from './base.service';

@Injectable()

export class FeatureAuthService extends BaseService {
  features: FeatureAccess = {
    users: 0,
    templates: 0,
    visits: 0,
    leads: 0,
    calculators: 0,
    analytics: false,
    cta: false,
    integrations: false,
    embedding: false,
    custom_branding: false,
    real_time_results: true,
    lead_generation: false,
    calc_in_limit: false,
    custom_font: false,
    background_tints: false,
    custom_colors: false,
  };

  constructor(private _http: Http, private _subDomainService: SubDomainService) {
    super();

  }

  getfeatureAccess(featureName: string): Observable<boolean> | boolean{
    let companyAccess = JSON.parse(this.readCookie('filepicker_token_json'));
    let subscription_status = '';
    companyAccess.forEach((e:any) => {
      if(e.key  === this._subDomainService.subDomain.sub_domain){
        subscription_status = e.value;
      }
    });
    if (subscription_status === 'active' || subscription_status === 'in_trial') {
      let company = localStorage.getItem('company');
      let getUrl = this._url + '/planfeature/check/' + company + '/' + featureName;
      return this._http.get(getUrl, this.options)
        .map(this.boolData)
        .catch(this.handleError);
    }else {
      return false;
    }
  }

  checkCalcLimit(): Observable<boolean> {
    let company = localStorage.getItem('company');
    let getUrl = this._url + '/plan/check/calc/' + company;
    return this._http.get(getUrl, this.options)
      .map(this.boolData)
      .catch(this.handleError);
  }

  getAllFeatureAccess() {
    let companyAccess = JSON.parse(this.readCookie('filepicker_token_json'));
    let subscription_status = '';
    if(!companyAccess)
      window.location.href = window.location.origin + '/logout';
    else
      companyAccess.forEach((e:any) => {
        if(e.key  === this._subDomainService.subDomain.sub_domain){
          subscription_status = e.value;
        }
      });

      let company = JSON.parse(this.readCookie('storage')).company;
      company.integration = company.integration ? company.integration : false;
    if (subscription_status === 'active' || subscription_status === 'in_trial') {
      let sub_domain = this._subDomainService.subDomain.sub_domain;
      let getUrl = this._url + '/planfeature/access/' + sub_domain;
      this._http.post(getUrl,{}, this.post_options())
        .map(this.extractData)
        .catch(this.handleError)
        .subscribe((result) => {
          this.features.users = result.users;
          this.features.templates = result.templates;
          if(company.current_limit.calculators === undefined || company.current_limit.calculators === null)
            this.features.calculators = result.calculators;
          else
            this.features.calculators = company.current_limit.calculators;
          this.features.visits = result.visits;
          this.features.leads = result.leads;
          this.features.analytics = result.analytics;
          this.features.embedding = result.embedding;
          this.features.cta = result.cta;
          this.features.integrations = result.integrations || company.integration;
          this.features.custom_branding = result.og_branding;
          this.features.real_time_results = true;
          this.features.lead_generation = result.lead_generation_viewing;
          this.features.calc_in_limit = result.calc_in_limit;
          this.features.custom_font = result.custom_styling;
          this.features.background_tints = result.custom_styling;
          this.features.custom_colors = result.custom_styling;
          //console.log(result, 'resultttttt');
        });
    }
  }

}



